import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import './Assets/Scss/index.scss';
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

serviceWorkerRegistration.register({
  onUpdate: async (registration) => {
    // Corremos este cdigo si hay una nueva versin de nuestra app
    // Detalles en: https://developers.google.com/web/fundamentals/primers/service-workers/lifecycle
    if (registration && registration.waiting) {
      await registration.unregister();
      registration.waiting.postMessage({ type: "SKIP_WAITING" });
      // Des-registramos el SW para recargar la pgina y obtener la nueva versin. Lo cual permite que el navegador descargue lo nuevo y que invalida la cache que tena previamente.
      window.location.reload();
    }
  },
});